<script>
import MinusIcon from "@/components/admin/icons/minus_icon";
import PlusIcon from "@/components/admin/icons/plus_icon";
import api from "@/service/api";
export default {
  name: "navigation-component",
  components: {PlusIcon, MinusIcon},
  data() {
    return {
      selectedPosition: 0,
      selectedInPosition: 0,
      isOpen: [false, false, false],
      companyName: ""
    }
  },
  mounted() {
    api.profile.getProfile().then((response) => {
      this.companyName = response.data.name
    })
  },
  methods: {
    showHide(position) {
      this.isOpen[position] = !this.isOpen[position];
    },
    selectNav(position) {
      this.selectedPosition = parseInt(position / 2);
      if (this.selectedPosition === 3) {
        this.selectedInPosition = -1;
      } else {
        this.selectedInPosition = position;
      }
      this.changeFragment(position)
    },
    changeFragment: function (position) {
      this.$emit('positionChanged', position)
    },
    logout() {
      localStorage.removeItem("auth_token");
      localStorage.removeItem("type");
      location.replace('/')
      // this.$router.push('/')
    }
  }
}
</script>

