<template>
  <cabinet-pass-password v-if="isShowPassAlert" :is-cancel="isPassCancel" :document-id="documentId" v-on:closePassAlert="showHidePassAlert"/>
  <div class="content">
    <table class="promo-table">
      <tr>
        <th>ID</th>
        <th>Тип</th>
        <th>Дата</th>
        <th>Подписали</th>
        <th>Статус</th>
        <th>Документ</th>
        <th></th>
        <th></th>
      </tr>
      <tr v-for="document in documents" :key="document">
        <td>{{document.id}}</td>
        <td>{{document.type}}</td>
        <td>{{millsToTime(document.createdDate)}}</td>
        <td>{{document.step + 1}} из {{document.stepSize}}</td>
        <td>{{document.status}}</td>
        <td><a :href="document.content">Открыть</a></td>
        <td><p class="pass-text" @click="passDocument(document.id)">Подписать</p></td>
        <td><p class="cancel-text" @click="cancelDocument(document.id)">Отменить</p></td>
      </tr>
    </table>
  </div>
</template>

<script>
import api from "@/service/api";
import CabinetPassPassword from "@/components/cabinet/cabinet_pass_password.vue";

export default {
  name: "cabinet-docs-for-me",
  components: {CabinetPassPassword},
  data() {
    return {
      documents: [],
      isShowPassAlert: false,
      isPassCancel: false,
      documentId: 0
    }
  },
  mounted() {
    this.loadDocuments()
  },
  methods: {
    loadDocuments() {
      api.docs.getDocsForMe().then((response) => {
        const list = response.data
        for (let i = 0; i < list.length; i++) {
          if (list[i].status !== "CANCELED" && list[i].status !== "READY") {
            this.documents.push(list[i])
          }
        }
      })
    },
    millsToTime(mills) {
      return new Date(mills).toString()
    },
    showHidePassAlert(isShow) {
      this.isShowPassAlert = isShow
    },
    cancelDocument(documentId) {
      this.isPassCancel = true
      this.documents = documentId
      this.showHidePassAlert(true)
    },
    passDocument(documentId) {
      this.isPassCancel = false
      this.documents = documentId
      this.showHidePassAlert(true)
    }
  }
}
</script>

